import React, { Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import Genre from '../Genre/Genre';

import axiosRequests from '../../../Requests/Requests';
import { elastic as Menu } from 'react-burger-menu';
import Routes from '../../Routes';

import styles from './Navigation.module.css';
import './Burger.css';

class Navigation extends React.Component {
    state = {
        configuration: {},
        genre: [],
    };

    componentDidMount() {
        axiosRequests
            .axiosGetConfiguration()
            .then(request =>
                this.setState({ configuration: request.data.images }),
            );
        axiosRequests.axiosGenreRequest().then(request => {
            this.setState({ genre: request.data.genres });
        });
    }

    render() {
        let { queue, watched } = Routes;
        let { configuration, genre } = this.state;

        if (this.props.onWidth < 768) {
            return (
                <Menu right>
                    <ul className={styles.wrapNav}>
                        <li className={styles.HeaderMenuHome}>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{ pathname: `${Routes.home}` }}
                            >
                                HOME
                            </NavLink>
                        </li>
                        <li className={styles.HeaderMenuHome}>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{ pathname: `${Routes.pageString}=${1}` }}
                            >
                                Trending
                            </NavLink>
                        </li>
                        <li className={styles.HeaderMenu}>
                            My Libbary
                            <div className={styles.hiddenHeaderMenu}>
                                <NavLink
                                    activeClassName={styles.menuIsActiv}
                                    to={{
                                        pathname: watched,
                                        state: { configuration },
                                    }}
                                >
                                    Watched
                                </NavLink>
                                <NavLink
                                    activeClassName={styles.menuIsActiv}
                                    to={{
                                        pathname: queue,
                                        state: { configuration },
                                    }}
                                >
                                    Queue
                                </NavLink>
                            </div>
                        </li>

                        <li className={styles.GengeMenu}>
                            Genre
                            <div className={styles.hiddenGengeMenu}>
                                <Genre
                                    genre={genre}
                                    onHendleGenre={this.props.onHendleGenre}
                                />
                            </div>
                        </li>
                    </ul>
                </Menu>
            );
        } else {
            return (
                <Fragment>
                    <ul className={styles.wrapNav}>
                        <li className={styles.HeaderMenuHome}>
                            <NavLink
                                exact
                                activeClassName={styles.menuIsActiv}
                                to={{ pathname: `${Routes.home}` }}
                            >
                                HOME
                            </NavLink>
                        </li>
                        <li className={styles.HeaderMenuHome}>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{ pathname: `${Routes.pageString}=${1}` }}
                            >
                                Trending
                            </NavLink>
                        </li>
                        <li className={styles.HeaderMenu}>
                            My Libbary
                            <div className={styles.hiddenHeaderMenu}>
                                <NavLink
                                    activeClassName={styles.menuIsActiv}
                                    to={{
                                        pathname: watched,
                                        state: { configuration },
                                    }}
                                >
                                    Watched
                                </NavLink>
                                <NavLink
                                    activeClassName={styles.menuIsActiv}
                                    to={{
                                        pathname: queue,
                                        state: { configuration },
                                    }}
                                >
                                    Queue
                                </NavLink>
                            </div>
                        </li>
                        <li className={styles.GengeMenu}>
                            Genre
                            <div className={styles.hiddenGengeMenu}>
                                <Genre
                                    genre={genre}
                                    onHendleGenre={this.props.onHendleGenre}
                                />
                            </div>
                        </li>
                    </ul>
                </Fragment>
            );
        }
    }
}

Navigation.propTypes = {
    genre: PropTypes.array,
    configuration: PropTypes.object,
    Routes: PropTypes.object,
    axiosRequests: PropTypes.object,
    styles: PropTypes.object,
};

export default Navigation;
