import requests from './requests.json';
import axios from 'axios';

const axiosGetConfiguration = () => {
    return axios.get(requests.configurationURL + requests.API_KEY);
};

const axiosLoadTrend = pageNumber => {
    let URI = getTrendingURI(pageNumber);
    return axios.get(URI, {});
};

const getTrendingURI = pageNumber => {
    return (
        requests.trendingURL +
        requests.API_KEY +
        requests.genrePage +
        pageNumber
    );
};

const axiosSearchRequest = (inputValue, pageNumber) => {
    let URI = getSearchURI(pageNumber, inputValue);
    return axios.get(URI);
};
const getSearchURI = (pageNumber, inputValue) => {
    return (
        requests.searchRequestURL +
        requests.API_KEY +
        `&query=${inputValue}&page=${pageNumber}`
    );
};

const axiosMovieDetails = idMovie => {
    let URI = requests.movieURL + idMovie + requests.API_KEY;
    return axios.get(URI, {});
};

const axiosGenreRequest = () => {
    let URI = requests.genre + requests.API_KEY;
    return axios.get(URI);
};

const axiosGenreRequestDetails = (genreID, pageNumber) => {
    let URI =
        requests.genreDetails +
        requests.API_KEY +
        requests.genresID +
        genreID +
        requests.genrePage +
        pageNumber;
    return axios.get(URI);
};

const axiosLoadHome = () => {
    let URI = requests.tvURL + requests.API_KEY;
    return axios.get(URI, {});
};

let axiosRequests = {
    axiosSearchRequest,
    axiosGetConfiguration,
    axiosLoadTrend,
    axiosMovieDetails,
    axiosGenreRequest,
    axiosGenreRequestDetails,
    axiosLoadHome,
};

export default axiosRequests;
