import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import SliderItems from './SliderItems';
import Routes from '../../Routes';

import TrendingMoviesItem from './TrendingMoviesItem';
import styles from './TrendingMovies.module.css';

export default class TrendingMovies extends Component {
    settings = {
        dots: false,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        speed: 2000,
        autoplaySpeed: 1,
        focusOnSelect: true,
        pauseOnHover: true,
        accessibility: true,
    };

    render() {
        let { arrMovies, configuration, location, search, onHendleSlider } =
            this.props;

        return (
            <div>
                {this.props.onWidth > 1200 &&
                    this.props.location === Routes.home &&
                    '/' && (
                        <div className={styles.containerSlider}>
                            <Slider
                                className={styles.sliderBody}
                                {...this.settings}
                            >
                                {arrMovies.length > 0 &&
                                    arrMovies.map(({ id, backdrop_path }) => {
                                        return (
                                            backdrop_path && (
                                                <SliderItems
                                                    key={id}
                                                    id={id}
                                                    backdrop_path={
                                                        configuration.secure_base_url +
                                                        '/w500/' +
                                                        backdrop_path
                                                    }
                                                    onHendleSlider={
                                                        onHendleSlider
                                                    }
                                                />
                                            )
                                        );
                                    })}
                            </Slider>
                        </div>
                    )}

                <div className={styles.mainTrending}>
                    <div className={styles.items}>
                        <ul
                            className={
                                this.props.location === Routes.home
                                    ? styles.itemsWrapHome
                                    : styles.itemsWrap
                            }
                        >
                            {arrMovies.length > 0 &&
                                arrMovies.map(
                                    ({
                                        id,
                                        poster_path,
                                        title,
                                        vote_average,
                                        release_date,
                                    }) => {
                                        return (
                                            <TrendingMoviesItem
                                                key={id}
                                                id={id}
                                                poster_path={poster_path}
                                                title={title}
                                                vote_average={vote_average}
                                                release_date={release_date}
                                                configuration={configuration}
                                                search={search}
                                                location={location}
                                            />
                                        );
                                    },
                                )}
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

TrendingMoviesItem.defaultProps = {
    title: '???',
    vote_average: '???',
    release_date: '???',
};

TrendingMoviesItem.propTypes = {
    title: PropTypes.string,
    vote_average: PropTypes.number,
    release_date: PropTypes.string,
};
