import React, { Component, Fragment } from 'react';

import Navigation from '../components/Header/Navigation/Navigation';
import Logo from '../components/Header/Logo/Logo';
import TrendingMovies from '../components/Main/TrendingMovies/TrendingMovies';
import Footer from '../components/Footer/Footer';
import NotFound from '../views/NotFound';

import Spinner from '../components/Main/Spinner/Spinner';

import stylesMain from '../components/Main/Main.module.css';
import styles from '../components/Header/Navigation/Navigation.module.css';

import Routes from '../components/Routes';
import axiosRequests from '../Requests/Requests';

export default class Home extends Component {
    state = {
        galleryMovies: [],
        configuration: {},
        loading: false,
        error: false,
        width: window.innerWidth,
    };
    componentDidMount() {
        window.addEventListener('resize', this.updSize);

        axiosRequests
            .axiosGetConfiguration()
            .then(request =>
                this.setState({ configuration: request.data.images }),
            );

        axiosRequests.axiosLoadHome().then(req => {
            this.setState({ galleryMovies: req.data.results });
        });
    }

    updSize = () => {
        this.setState({
            width: window.innerWidth,
        });
    };

    componentWillUnmount() {
        window.removeEventListener('resize', this.updSize);
    }

    handleGenre = (genreID, page = 1) => {
        this.props.history.push({
            pathname: Routes.genrString + '/' + page + '/' + genreID,
            search: '',
        });
    };

    render() {
        let { width, loading, configuration, galleryMovies, error } =
            this.state;
        return (
            <Fragment>
                <header>
                    <div className={styles.wrapHeader}>
                        <Logo />
                        <Navigation
                            onWidth={width}
                            onHendleGenre={this.handleGenre}
                        />
                    </div>
                </header>
                <main>
                    <div className={stylesMain.wrapMain}>
                        {error && <NotFound />}
                        {loading && <Spinner />}
                        {!loading && galleryMovies.length === 0 && <NotFound />}
                        {!loading && galleryMovies.length > 0 && (
                            <TrendingMovies
                                location={this.props.location.pathname}
                                arrMovies={galleryMovies}
                                configuration={configuration}
                                onWidth={width}
                            />
                        )}
                    </div>
                </main>
                <footer>
                    <Footer />
                </footer>
            </Fragment>
        );
    }
}
