import React from 'react';
import { NavLink } from 'react-router-dom';
import Routes from '../../Routes';

export default function SliderItems({ backdrop_path, id }) {
    return (
        <NavLink
            to={{
                pathname: `${Routes.movieString}/${id}`,
                search: '',
            }}
        >
            <img src={backdrop_path} alt=""></img>
        </NavLink>
    );
}
